.row{
    margin-left: 20px;
    color: white; 
    .row__posters{
        display: flex;
        justify-content: flex-start;
        overflow-y: hidden;
        overflow-x: scroll;
        padding: 20px;
        .row__poster{
            object-fit: contain;
            width: 100%;
            max-height: 150px;
            transition: transform 450ms;
            margin-right: 10px;
            cursor: pointer;
        }
        .row__poster:hover{
            transform: scale(1.08);
        }
        .row__posterLarge{
            max-height: 250px;
        }
        .row__posterLarge:hover{
            transform: scale(1.09);
        }
    }
    .row__posters::-webkit-scrollbar{
        display: none;
    }
}